import { FacebookProvider, Like } from "react-facebook";

import styled from "styled-components";

const FacebookProviderDiv = styled.div`
  background: orange;
  .facebookProviderClassname {
    width: 1vw;
    overflow: hidden;
    margin-left: 50%;

    transform: (-50%, -50%);
  }
`;

function FacebookLike() {
  return (
    <FacebookProviderDiv>
      <FacebookProvider appId="379850369411744">
        <div className="facebookProviderClassname">
          <Like
            href="https://www.facebook.com/revelermusicllc"
            colorScheme="light"
            showFaces
            share
          />
        </div>
      </FacebookProvider>
    </FacebookProviderDiv>
  );
}

export default FacebookLike;
